/* global Office, window, localStorage */

/**
 * Shows a notification when the add-in command is executed.
 * @param event
 */

import { getClassification } from "../../utils/getClassification";
import { updateSignature } from "../../utils/htmlSignature";
import { getAttachments } from "../../utils/readAttachments";
import { CountType, getWarningMessages } from "./getWarningMessages";

export const onSendValidateAttachments = async (event: Office.AddinCommands.Event) => {
  try {
    console.log("Entrando al send attachment--->");

    const attachments = (await getAttachments()) || [];
    console.log("Entrando al send attachment--->", attachments);

    // TODO: Probar esto: (H) ho me ha ocurrido, ---> aqui hay problemas cuando se adjuntan mas de un file de la misma clasificacion

    const attachmentsWithContentAndClassification = await Promise.all(
      attachments.map(async (attachment) => ({
        ...attachment,
        classification: (await getClassification(attachment)).classificationLabel,
      }))
    );
    console.log("Entrando al classificationList--->", attachmentsWithContentAndClassification);

    const classificationList = attachmentsWithContentAndClassification.map((attachment) => ({
      classification: attachment.classification,
      name: attachment.name,
    }));

    const classificationCount = classificationList.reduce((acc: CountType, obj) => {
      const key = obj.classification;
      if (!acc[key]) {
        acc[key] = [];
      }
      // Add object to list for given key's value
      acc[key].push(obj.name);
      return acc;
    }, {});
    console.log("ClassificationCount--->", classificationCount);

    const message = getWarningMessages(classificationCount);
    localStorage.setItem("mailTagText", "");

    console.log("Message--->", message);
    await updateSignature(attachmentsWithContentAndClassification);
    if (message) {
      // Block send.
      let dialog: Office.Dialog;
      Office.context.ui.displayDialogAsync(
        window.location.origin + `/taskpane.html?specificComponent=modal&message=${message}`,
        {
          asyncContext: event,
          displayInIframe: true,
          height: 30,
          width: 40,
        },
        (asyncResult) => {
          dialog = asyncResult.value;
          // Pass item body to dialog by localStorage
          dialog.addEventHandler(Office.EventType.DialogMessageReceived, (message) => {
            // Our logic is handled within the component,
            // on clicking either button, we send a "sussess" message,
            // nothing to do here but closing the dialog and allowing the item to be sent
            if ("error" in message) {
              dialog.close();
              asyncResult.asyncContext.completed({ allowEvent: false });
            } else if (message.message === "cancel") {
              dialog.close();
              asyncResult.asyncContext.completed({ allowEvent: false });
            } else if (message.message === "continue") {
              dialog.close();
              asyncResult.asyncContext.completed({ allowEvent: true });
            } else {
              dialog.close();
              asyncResult.asyncContext.completed({ allowEvent: false });
            }
          });

          // If user closes the dialog with top-right X, prevent send
          dialog.addEventHandler(Office.EventType.DialogEventReceived, () => {
            asyncResult.asyncContext.completed({ allowEvent: false });
          });
        }
      );
    } else {
      // Allow send.
      event.completed({ allowEvent: true });
    }
  } catch (error) {
    console.log("Error onSendValidateAttachments--->", error);
    // Allow send.
    event.completed({ allowEvent: true });
  }
};
