/* global Office, console */

import { getNameClassificationByDocumentName } from "../api/nameClassification";
import { ClassificationLabel } from "../../types/classificationType";
export const getCloudClassification = async (attachment: Office.AttachmentDetailsCompose) => {
  const profile = Office.context.mailbox.userProfile;
  const classification: ClassificationLabel = await getNameClassificationByDocumentName(
    attachment.name,
    profile.emailAddress
  );
  return classification;
};
