import { ClassificationLabel } from "../types/ClassificationType";
import { addSignature, getBody, obtaintSignature } from "./addBody";

export type AttachmentAndClassification = Office.AttachmentDetailsCompose & { classification: ClassificationLabel };

export const updateSignature = async (attachments: AttachmentAndClassification[]) => {
  try {
    const messageBody = await getBody();
    console.log("UpdateSignature Body------>", messageBody);
    const oldSignature = obtaintSignature(messageBody.value);
    console.log("UpdateSignature oldSignature------>", oldSignature);
    const cleanSignature = clearSignature(oldSignature);
    console.log("UpdateSignature cleanSignature------>", cleanSignature);
    if (attachments.length === 0) {
      await addSignature(cleanSignature);
    } else if (attachments.length > 0) {
      const enhancedSignature = createSignature(cleanSignature, attachments);
      console.log("UpdateSignature enhancedSignature------>", cleanSignature);
      await addSignature(enhancedSignature);
    }
  } catch (err) {
    console.log("UpdateSignature Error in signature------>", err);
  }
};

export const createSignature = (oldSignature: string, attachments: AttachmentAndClassification[]) =>
  `
<b>Etiquetas de documentos adjuntos:</b>
<p style='maring-top: 20px'>
${attachments.map((attachment) => {
  const style = attachmentsStyles(attachment);
  if (!style) return ""; //Not classified documents
  return `<span style='align-items:center; padding-right: 20px;'>${attachment.name + "&nbsp"}<b style="color:${
    style.color
  };">(${style.name})</b></span>`;
})}
</p>
<div id="close"></div>`.replace(/,/g, "") +
  `${oldSignature}
`;

