export const clearSignature = (oldSignature: string) =>
  oldSignature
    .replace(/\n/g, "")
    .replace(/<b>Etiquetas de documentos adjuntos:<\/b>(.*)<div id="x_close"><\/div>/, "")
    .replace(/\n/g, "");

const attachmentsStyles = (attachment: AttachmentAndClassification) => {
  let arr: { color: string; name: string } | null = null;
  switch (attachment.classification) {
    case "public":
      arr = {
        color: "#107c10",
        name: "Pblico",
      };
      break;
    case "restricted":
      arr = {
        color: "#d83b01",
        name: "Restringido",
      };
      break;
    case "confidential":
      arr = {
        color: "#e81123",
        name: "Confidencial",
      };
      break;
    case "internalUse":
      arr = {
        color: "#d29200",
        name: "Uso interno",
      };
      break;
    default:
      null;
  }
  return arr;
};
