import "whatwg-fetch"; // Polyfill to fetch, TODO: move to webpack

import config from "../../config";
import { fromNumberToClassification } from "../classificationMap";

export const getNameClassificationByDocumentName = async (fileName: string, email?: string) => {
  const domain = Office.context.mailbox.userProfile.emailAddress.split("@")[1]; // @outlook.com
  const body = email
    ? {
        mail: email,
        name: fileName,
      }
    : { name: fileName };

  const requestOptions = {
    method: "post",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "x-api-key": config.api_key,
      domain,
    },
    body: JSON.stringify({ documents: [body] }),
  };

  try {
    let response = await fetch(config.api_classification, requestOptions);
    const data = (await response.json()) as any;
    const classificationFromEndpoint =
      data && data.documents && data.documents.length > 0 ? data.documents[0].classification_value : null; // TODO this should change once that the behaviour of search in batch is enabled
    const classification = fromNumberToClassification(classificationFromEndpoint);
    return classification;
  } catch (err) {
    console.log("Error en getNameClassificationByDocumentName", err);
    return null;
  }

  // return data.clasificacion ? "confidential" : "public";
};
