import { ClassificationLabel } from "../types/ClassificationType";

export const fromNumberToClassification = (number: number): ClassificationLabel => {
  switch (number) {
    case 0:
      return "public";
    case 1:
      return "internalUse";
    case 2:
      return "restricted";
    case 3:
      return "confidential";
    case -1:
      return null; // If not classification was found
    default:
      return null;
  }
};
