import { ClassificationLabel } from "../../types/ClassificationType";
export type CountType = { [key in ClassificationLabel]?: string[] };

export const getWarningMessages = (count: CountType): string => {
  console.log("XXXX------>", count);
  //const restrictedFields = !!count.confidential?.length;
  let countClasificated = 0;
  let messagePopUp: string;

  if (count.confidential?.length > 0 || count.restricted?.length > 0) {
    countClasificated += count.confidential?.length > 0 ? count.confidential?.length : 0;
    countClasificated += count.restricted?.length > 0 ? count.confidential?.length : 0;
    messagePopUp = `Ten en cuenta lo siguiente: Este mensaje contiene (${countClasificated}) archivos adjuntos que pueden contener informacin sensible para la compaa.`;
  }
  //return restrictedFields ? `Existen (${count.confidential.length}) archivos restringidos` : null;
  return countClasificated > 0 ? messagePopUp : null;
};
