/* global Office, console */
import { ClassificationType } from "../types/ClassificationType";
import { getCloudClassification } from "./classifications/CloudClassification";
import { getEdgeClassification } from "./classifications/EdgeClassification";
import { getIEClassification } from "./classifications/IEClassification";
import { isIE } from "./isIE";

export const getClassification = async (attachment: Office.AttachmentDetailsCompose): Promise<ClassificationType> => {
  const defaultClassification: ClassificationType = { classificationLabel: null, classificationSource: "na" };
  try {
    switch (attachment.attachmentType) {
      case Office.MailboxEnums.AttachmentType.Cloud:
        const classificationLabel = await getCloudClassification(attachment);
        return { classificationLabel, classificationSource: "name" };

      case Office.MailboxEnums.AttachmentType.Item:
        return defaultClassification;

      case Office.MailboxEnums.AttachmentType.File:
        if (isIE) {
          const classificationLabel = await getIEClassification(attachment);
          return { classificationLabel, classificationSource: "name" };
        } else {
          const classificationLabel = await getEdgeClassification(attachment);
          return { classificationLabel, classificationSource: "content" };
        }
      default:
        // Handle attachment formats that are not supported.
        return defaultClassification;
    }
  } catch (err) {
    console.debug("Error clasificando", err);
    return defaultClassification;
  }
};
