/* global Office */

import { toPromise } from "./utils";

export const addSignature = async (text: string) => {
  const item = Office.context.mailbox.item;
  item.body.setSignatureAsync(text, { coercionType: Office.CoercionType.Html, asyncContext: { var3: 1, var4: 2 } });
};

export const getBody = async () => {
  const item = Office.context.mailbox.item;
  const body = await toPromise(item.body.getAsync, Office.CoercionType.Html);
  return body;
};

export const obtaintSignature = (body: string) => {
  const wrapper = document.createElement("div");
  wrapper.innerHTML = body;
  return wrapper.querySelector("#Signature")?.innerHTML || "";
};
